.left-dots-div{
    padding: 20px 20px 0px;
}
.left-dots{
    height: 110px;
}
.right-dots-div{
    padding: 0px 20px 20px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}
.right-dots{
    position: absolute;
    height: 110px;
    width: 110px;
    transform: translateY(-120px);
}
.values-div{
    text-align: center;
    transform: translateY(-100px);
}
.values-cards{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    padding-top: 50px;
}
.cards{
    background-color: #fff;
    min-height: 300px;
    width:22%;
    box-shadow: 4px 4px 30px 0px rgba(0, 0, 0, 0.1);
    border-radius: 9px;
    padding: 20px;
    text-align: left;
}
.cards .icon{
    width: fit-content;
    background-color: #14213D;
    padding: 15px;
    border-radius: 50px;
}
.icon img{
    height:30px;
    width:30px;
}
.cards h4{
    color: rgba(20, 33, 61, 0.7);
    margin-top: 10px;
}

@media(max-width:768px){
    .values-cards{
        display: block;
    }
    .cards{
        width: 50%;
        margin: 30px auto;
    }
}

@media(max-width:425px){
    .values-div{
        transform: translateY(-70px);
    }
    .left-dots{
        height: 80px;
    }
    .right-dots{
        height: 80px;
        width: 80px;
    }
    .cards{
        width: 60%;
        margin: 30px auto;
    }
    .values-cards{padding-top: 25px;}
}

@media(max-width:375px){
    .values-div{
        transform: translateY(-50px);
    }
    .cards{
        width: 75%;
    }
    .values-cards{padding-top: 15px;}
}